import axios from 'axios';

class EndlessCreation {
  constructor() {
    this.baseURL = process.env.REACT_APP_BASE_URL;
    this.accessToken = '';
  }

  setAccessToken(accessToken) {
    this.accessToken = accessToken;
  }

  async login(providerName) {
    const response = await axios({
      method: 'get',
      url: `${this.baseURL}/oauth/${providerName}`,
    });
    this.setAccessToken(response.data.accessToken);
    return response.data;
  }

  async githubLogin(c) {
    const response = await axios({
      method: 'post',
      url: 'https://github.com/login/oauth/access_token',
      params: {
        client_id: 'fabf00d37c1112717968',
        client_secret: '37e18fd18c19ceff1eb7af5ddfe63a6300cd580d',
        code: c,
        redirect_uri: 'http://localhost:3000/todo_list_for_react_seminar/',
      },
    });
    this.setAccessToken(response.data.accessToken);
    return response.data;
  }

  async postOauthToken(providerName, token) {
    const response = await axios({
      method: 'post',
      url: `${this.baseURL}/oauth/${providerName}`,
      data: {
        token_type: token.token_type,
        access_token: token.access_token,
        scope: token.scope,
        expires_in: token.expires_in,
        id_token: token.id_token,
        expires_at: token.expires_at,
      },
    });
    return response.data;
  }

  async postTodoList(todo) {
    const response = await axios({
      method: 'post',
      url: `${this.baseURL}/todos`,
      headers: {
        Authorization: `Bearer ${this.accessToken}`,
      },
      data: {
        title: todo.title,
        description: todo.description,
        writer: todo.writer,
      },
    });
    return response.data;
  }

  async getTodoList() {
    const response = await axios({
      method: 'get',
      url: `${this.baseURL}/todos`,
      headers: {
        Authorization: `Bearer ${this.accessToken}`,
      },
    });
    return response.data;
  }

  async getTodo(id) {
    const response = await axios({
      method: 'get',
      url: `${this.baseURL}/todos/${id}`,
      headers: {
        Authorization: `Bearer ${this.accessToken}`,
      },
    });
    return response.data;
  }

  async patchTodo(todo) {
    const response = await axios({
      method: 'patch',
      url: `${this.baseURL}/todos/${todo.id}`,
      headers: {
        Authorization: `Bearer ${this.accessToken}`,
      },
      data: {
        title: todo.title,
        description: todo.description,
        writer: todo.writer,
      },
    });
    return response;
  }

  async patchTodoComplete(id, isCompleted) {
    const response = await axios({
      method: 'patch',
      url: `${this.baseURL}/todos/${id}/complete`,
      headers: {
        Authorization: `Bearer ${this.accessToken}`,
      },
      data: {
        isCompleted: isCompleted,
      },
    });
    return response;
  }

  async deleteTodo(id) {
    const response = await axios({
      method: 'delete',
      url: `${this.baseURL}/todos/${id}`,
      headers: {
        Authorization: `Bearer ${this.accessToken}`,
      },
    });
    return response;
  }
}

export default EndlessCreation;
